@import 'base';

.ssm {
  background: url('../images/universe--mobile.jpg') top center no-repeat;
  background-size: cover;
  height: 100vh;
  position: relative;
  overflow: hidden;
  width: 100vw;

  @media (min-width: 768px) {
    background: url('../images/universe--tablet.jpg') top center no-repeat;
    background-size: cover;
  }
  
  @media (min-width: 1024px) {
    background: url('../images/universe--desktop-sm.jpg') top center no-repeat;
    background-size: cover;
  }
  
  @media (min-width: 1440px) {
    background: url('../images/universe--desktop-md.jpg') 0 no-repeat;
    background-size: cover;
  }
  
  @media (min-width: 1920px) {
    background: url('../images/universe--desktop-lg.jpg') top center no-repeat;
    background-size: cover;
  }

  &-logo--wrapper {
    @media (min-width: 568px) {
      display: flex;
      flex-direction: row;
      height: 100%;
      position: absolute;
      left: 50%;
      top: 48%;
      transform: translate(-50%, -50%);
      transition: all .25s ease-out;
      width: 100%;
    }
    
    @media (min-width: 768px) {
      flex-direction: column;
      position: absolute;
      left: 50%;
      top: 70%;
      transform: translate(-50%, -50%);
      transition: all .25s ease-out;
      width: 100%;
    }
    
    @media (min-width: 1024px) {
      display: flex;
      flex-direction: row;
      height: 100%;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      transition: all .25s ease-out;
      width: 100%;
    }
    
    @media (min-width: 1440px) {
      display: flex;
      flex-direction: row;
      height: 100%;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      transition: all .25s ease-out;
      width: 100%;
    }

    &[data-active='true'] {
      left: 15%;
    }
  }

  &-logo {
    height: auto;
    padding-top: 25%;
    margin: auto;
    min-height: 45vh;
    position: relative;
    width: 90%;

    @media (min-width: 568px) {
      margin: auto 0;
      min-height: 100vh;
      padding-top: 0;
      width: 60%;
    }

    @media (min-width: 768px) {
      margin: 0;
      min-height: 40vh;
      padding-top: 0;
      width: 90%;
    }
    
    @media (min-width: 1024px) {
      margin: 0;
      padding-top: 0;
      width: 60%;
    }
    
    @media (min-width: 1440px) {
      min-height: inherit;
      width: 65%;
    }
    
    @media (min-width: 1920px) {
      width: 80%;
    }

    &--svg {
      bottom: 0;
      fill: $ssm-lightest;
      left: 0;
      position: absolute;
      width: 10%;

      @media (min-width: 568px) {
        bottom: auto;
        left: 55%;
        position: absolute;
        top: 44%;
        transform: translate(-50%, -50%);
        width: 10%;
      }
      
      @media (min-width: 768px) {
        top: 30%;
        width: 10%;
      }
      
      @media (min-width: 1024px) {
        bottom: auto;
        left: 55%;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 10%;
      }
      
      @media (min-width: 1440px) {
        bottom: auto;
        left: 53%;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 10%;
      }
    }
  }

  &-mission--wrapper {
    opacity: 0;
    position: absolute;
    left: 40%;
    top: 200%;
    transform: translate(-50%, -0%);
    
    @media (min-width: 568px) {
      opacity: 0;
      position: absolute;
      left: 50%;
      top: 55%;
      transform: translate(-50%, -50%);
      width: 60%;
    }
    
    @media (min-width: 768px) {
      opacity: 0;
      position: absolute;
      left: 55%;
      top: 200%;
      transform: translate(-50%, -50%);
      width: 90%;
    }
    
    @media (min-width: 1024px) {
      opacity: 0;
      position: absolute;
      left: 45%;
      top: 60%;
      transform: translate(-50%, -50%);
      width: 55%;
    }
    
    @media (min-width: 1440px) {
      opacity: 0;
      position: absolute;
      top: 60%;
      transform: translate(-50%, -50%);
    }
    
    @media (min-width: 1920px) {
      left: 50%;
      width: 65%
    }
  }

  &-mission {
    line-height: 1.5;
    margin: 0;
    padding: 35px 40px 20px 40px;
    position: relative;

    @media (min-width: 568px) {
      padding: 0;
      width: 50%;
    }

    @media (min-width: 768px) {
      padding: 35px 350px 20px 80px;
    }
    
    @media (min-width: 1024px) {
      padding: 0;
    }
    
    @media (min-width: 1440px) {
      padding: 0;
      width: 45%;
    }
    
    @media (min-width: 1920px) {
      padding: 0;
      width: 40%;
    }

    &--statement {
      @include Europa-Bold;
      color: $ssm-lightest;
      font-size: 18px;
      line-height: 1.5;
      margin: 0 0 30px 0;

      @media (min-width: 568px) {
        font-size: 18px;
        line-height: 1.5;
        margin: 0 0 15px 0;
      }
      
      @media (min-width: 768px) {
        font-size: 24px;
        line-height: 1.5;
        margin: 0 0 15px 0;
      }

      @media (min-width: 1024px) {
        font-size: 24px;
        line-height: 1;
        margin: 0 0 15px 0;
      }
      
      @media (min-width: 1440px) {
        font-size: 30px;
        line-height: 1.25;
        margin: 0 0 15px 0;
      }
      
      @media (min-width: 1920px) {
        font-size: 50px;
        line-height: 1.25;
        margin: 0 0 30px 0;
      }
    }
  }
  
  &-action {
    margin: 0;
    padding: 0;
    position: relative;

    &--link {
      background: none;
      border: none;
      @include Europa-Bold;
      color: rgba($ssm-lightest, .50);
      font-size: 14px;
      margin: 0;
      padding: 0;
      text-decoration: none;
      transition: all .25s ease-out;

      @media (min-width: 768px) {
        font-size: 16px;
      }

      &:hover {
        color: rgba($ssm-lightest, 1);
      }

      &[data-action-type='instagram'] {
        .ssm-action--link_text {
          color: $ssm-magenta;
        }

        &:hover {
          color: $ssm-magenta;
        }
      }
      
      &[data-action-type='twitter'] {
        .ssm-action--link_text {
          color: $ssm-cyan;
        }

        &:hover {
          color: $ssm-cyan;
        }
      }
    }
  }

  &-slideout {
    @include Fira-Mono-OT-Regular;
    background: $ssm-key;
    bottom: -90%;
    height: 100vh;
    left: 0;
    position: absolute;
    transition: all .25s ease-out;
    width: 100vw;
    z-index: 99;

    @media (min-width: 375px) {
      bottom: -92%;
    }
    
    @media (min-width: 400px) {
      bottom: -93%;
    }
    
    @media (min-width: 414px) {
      bottom: -92%;
    }
    
    @media (min-width: 568px) {
      bottom: -85%;
    }
    
    @media (min-width: 768px) {
      bottom: -90%;
    }

    @media (min-width: 1024px) {
      bottom: 0;
      width: 45vw;
      left: 94%;
    }
    
    @media (min-width: 1440px) {
      bottom: 0;
      width: 40vw;
      left: 95%;
    }
    
    @media (min-width: 1920px) {
      left: 97%;
    }

    &:hover {
      cursor: pointer;
    }

    &[data-active='true'] {
      bottom: 0;

      @media (min-width: 1024px) {
        left: 55%;
      }
      
      @media (min-width: 1440px) {
        left: 60%;
      }

      .ssm-slideout--toggle {
        &:after {
          transform: rotateX(180deg) rotateY(180deg) rotateZ(0deg);
          transition-delay: .15s;

          @media (min-width: 1024px) {
            left: 50%;
            transform: rotateX(0deg) rotateY(-180deg) rotateZ(-90deg);
          }
          
          @media (min-width: 1440px) {
            left: 50%;
            transform: rotateX(0deg) rotateY(-180deg) rotateZ(-90deg);
          }
        }
      }
    }

    &--toggle {
      border-bottom: thin solid $ssm-darker;
      border-top: thin solid $ssm-darker;
      color: $ssm-lightest;
      text-align: center;
      padding: 15px 0 40px 0;
      position: relative;

      @media (min-width: 768px) {
        padding: 80px 0 20px 0;
      }
      
      @media (min-width: 1024px) {
        border-bottom: none;
        border-top: none;
        border-left: thin solid $ssm-darker;
        border-right: thin solid $ssm-darker;
        height: 100vh;
        left: 0;
        position: absolute;
        width: 60px;
        z-index: 99;
      }
      
      @media (min-width: 1440px) {
        border-bottom: none;
        border-top: none;
        border-left: thin solid $ssm-darker;
        border-right: thin solid $ssm-darker;
        height: 100vh;
        left: 0;
        position: absolute;
        width: 70px;
      }
      
      @media (min-width: 1920px) {
        width: 90px;
      }

      &:after {
        content: '^';
        color: $ssm-lightest;
        height: 12px;
        left: 50%;
        position: absolute;
        top: 45%;
        transform: rotateX(0deg) rotateY(180deg) rotateZ(0deg);
        transform-style: preserve-3d;
        transform-origin: 25% 25%;
        transition: all .45s ease-out;
        width: 20px;

        @media (min-width: 1024px) {
          left: 35%;
          top: 50%;
          transform: rotateX(0deg) rotateY(0deg) rotateZ(-90deg);
        }
        
        @media (min-width: 1440px) {
          left: 40%;
          top: 50%;
          transform: rotateX(0deg) rotateY(0deg) rotateZ(-90deg);
        }
      }
    }

    &--form {
      color: $ssm-lightest;
      font-size: 10px;
      margin: 0 0 50px 0;
      padding: 25px;

      @media (min-width: 768px) {
        font-size: 16px;
        padding: 80px;
      }
      
      @media (min-width: 1440px) {
        margin: auto;
        padding: 250px 80px;
        width: 70%;
      }

      &_prompt {
        margin: 40px 0;
      }

      &_field-wrapper {
        display: flex;
        flex-grow: 1;
        margin: 35px 0;
        position: relative;
      }

      &_name-lbl, &_message-lbl {
        flex-grow: 1;
        flex-basis: 20%;
      }

      @keyframes pulsate {
        0% {
          opacity: 0;
        }
        100% {
          opacity: 1;
        }
      }
      
      &_field-insert {
        // animation: pulsate infinite 1.5s;
        background: $ssm-lightest;
        height: 30px;
        left: 130px;
        opacity: 0;
        position: absolute;
        top: 0;
        width: 2px;
      }

      &_name-fld, &_message-fld {
        background: transparent;
        outline: thin solid $ssm-darker;
        border: none;
        color: $ssm-lightest;
        flex-grow: 2;
        flex-basis: 70%;
        height: 30px;
        margin: 0;
        padding: 0;
      }
    }

    &--social {
      padding: 25px;
      position: relative;

      @media (min-width: 768px) {
        padding: 80px;
      }

      @media (min-width: 1440px) {
        margin: auto;
        padding: 200px 80px;
        width: 70%;
      }

      &_header {
        color: $ssm-lightest;
        font-size: 10px;

        @media (min-width: 768px) {
          font-size: 16px;
        }
      }

      .ssm-action {
        display: inline-block;
        margin-right: 10px;
      }
    }
  }
}